.app-header {
  .logo {
    font-size: 40px;
  }

  .logo {
    margin-right: 8px;
  }
  .gapper {
    gap: 40px;
    flex-wrap: wrap;

    div{
      cursor: pointer;
      transition: all 0.5s;
      &:hover {
        color: $clr3;
      }
    }
    
  }

  button {
    border: 1px solid $clr5;
    color: $clr5;
    background-color: transparent;
    border-radius: 20px;
    padding: 10px 30px;
    transition: all 0.5s;
    &:hover {
      background-color: $clr5;
      color: #002529;
    }
  }
}
@media (min-width:560px) {
  .app-header{
    img {
      display: none;
    }
  }

}

@media (max-width:560px) {
  .app-header{
    .gapper {
      display: none;
    }

    button{
      display: none;
    }
  }
}