.build-wrapper {
  background-color: $clr2;

  position: relative;

  .background-circle {
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 0;
  }

  .build-container {
    z-index: 1;
    position: relative;
    padding: 205px 0;

    .build-title {
      font-size: 48px;
      margin-right: 2rem;
    }

    h3 {
      color: $clr3;
      margin-bottom: 1rem;
    }
    p {
      z-index: 1;
      position: relative;
      margin-bottom: 1rem;
   
   }


   .build-content {
     img {
       margin-right: 1rem;
     }
   }
  }
}

@media (max-width: 860px) {
  .build-wrapper {
    .background-circle {
      top: 0;
      right: 0;
    }

    .build-container {
      flex-wrap: wrap;
      padding: 64px 0;

      .build-title {
        padding-bottom: 56px;
        margin-right: 7rem;
        
        h1 {
          font-size: 32px;
          font-style: normal;
          font-weight: 700;
          line-height: 32px;
        }
      }

      .build-content {
        // flex-direction: column;
        justify-content: center;
        text-align: center;
// margin-left: 2rem;
        
      }
    }
  }
}
@media (max-width: 375px) {
  .build-wrapper {
    position: relative;
    .background-circle {
      top: 0;
      right: 0;
    }

    .build-container {
      flex-wrap: wrap;
      padding: 64px 0;

      .build-title {
        padding-bottom: 56px;
        h1 {
          font-size: 32px;
          font-style: normal;
          font-weight: 700;
          line-height: 32px;
        }
      }

      .build-content {
        flex-direction: column;
        justify-content: center;
        text-align: center;

        img {
          margin: 0;
        }
      }
    }
  }
}
