.flex {
  display: flex;
}

.flex.justify-center {
  justify-content: center;
}

.flex.align-center {
  align-items: center;
}

.flex.space-between {
  justify-content: space-between;
}

.flex.direction-col {
  flex-direction: column;
}

.flex.direction-row {
  flex-direction: row;
}

.flex.space-evenly {
  justify-content: space-evenly;
}
.clean-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.clean-link {
  text-decoration: none;
  color: inherit;
}

.heading-hr {
  height: 4px;
  background-color: $clr3;
  width: rem(50px);
  margin-bottom: 1rem;
}

.main-layout {
  max-width: 1020px;
  margin: 0 auto;
  padding: 0 auto;
}

@media (max-width: 1020px) {
  .main-layout {
    max-width: 860px;
    margin: 0 auto;
    padding: 0 auto;
  }
}
@media (max-width: 860px) {
  .main-layout {
    max-width: 600px;
    margin: 0 auto;
    padding: 0 auto;
  }
}
@media (max-width: 560px) {
  .main-layout {
    max-width: 375px;
    margin: 0 auto;
    padding: 0 auto;
  }
}



@media (max-width:375px) {
  .main-layout {
    max-width: 300px;
    margin: 0 auto;
    padding: 0 auto;
  }

}