.get-started {
  background-color: $clr3;
  color: #012f34;
  width: 100%;
  background-image: url('../../assets/imgs/bg-pattern-home-6-about-5.svg');
  background-repeat: no-repeat;
  background-position-y: bottom;
  z-index: 3;
  .title {
    // margin-left: 6rem;
    h1 {
      font-size: 48px;
      font-style: normal;
      font-weight: 700;
      line-height: 48px;
    }
  }

  .get-started-container {
    // position: absolute;
    // z-index: 3;

    height: 200px;
    button {
      background-color: transparent;
      border: 1px solid #012f34;
      color: #012f34;
      padding: 0.5rem 1.5rem;
      border-radius: 20px;
      // font-family: Livvic;
      font-size: 18px;
      font-style: normal;
      font-weight: 600;
      line-height: 28px;

      &:hover {
        border: 1px solid #012f34;
        background-color: #012f34;
        color: $clr5;
      }
    }
  }
}
@media (max-width: 1360px) {
  .get-started-container {
    .title {
      margin-left: 2rem;
    }
  }
}
@media (max-width: 1260px) {
  .get-started-container {
    .title {
      margin-left: 5rem;
    }
  }
}
@media (max-width: 1130px) {
  .get-started-container {
    .title {
      margin-left: 10rem;
    }
  }
}
@media (max-width: 1020px) {
  .get-started-container {
    .title {
      margin-left: 8rem;
    }
  }
}
@media (max-width: 860px) {
  .get-started-container {
    button {
      margin-bottom: 1.5rem;
    }
    .title {
      // margin-left: 8rem;
      margin-bottom: 1.5rem;
      margin-left: 0;
      h1 {
        font-family: Livvic;
        font-size: 32px;
        font-style: normal;
        font-weight: 700;
        line-height: 32px;
        letter-spacing: 0px;
        text-align: left;
      }
    }
  }
}

@media (max-width: 760px) {
  .get-started-container {
    .title {
      margin-left: 2rem;
    }
  }
}

@media (max-width: 680px) {
  .get-started-container {
    .title {
      margin-left: 3rem;
    }
  }
}
@media (max-width: 375px) {
  .get-started {
    .get-started-container {
      height: 300px;
      flex-direction: column;
      justify-content: center;
      .title {
        margin-left: 0;
      }
    }
  }
}
