* {
  box-sizing: border-box;
}

html {
  font-family: livvic, sans-serif;
  font-size: 18px;
}

body {
  margin: 0;
  background-color: #014e56;
  color: $clr5;
}

button {
  border: none;
  cursor: pointer;
}

table,
th,
td {
  border: 1px solid black;
}

td {
  border-collapse: collapse;
}

td {
  padding: 10px;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
div {
  margin: 0;
  cursor: default;
}
