.hero-wrapper {
  background-image: url('../../assets/imgs/bg-pattern-home-1.svg');
  background-repeat: no-repeat;
  background-position: left;
  background-position-x: -100px; // transform: translateX(-10px);
  position: relative;

  .bottom-svg {
    position: absolute;
    bottom: 0;
    right: 0;
  }
}

.hero-container {
  padding: 13rem 0;
  justify-content: space-between;
  .hero-heading {
    max-width: rem(600px);
    // margin-right: 2rem;
    h1 {
      font-size: 100px;
      line-height: 100px;
    }

    span {
      color: $clr3;
    }
  }

  .hero-txt {
    max-width: rem(390px);
  }
}

.left-svg {
  position: absolute;
  left: 0;
  //   z-index: 0;
}

@media (max-width: 1020px) {
  .hero-wrapper {
    background: none;
  }
  // .hero-container {

  // }
}
@media (max-width: 860px) {
  .hero-container {
    flex-wrap: wrap;
    text-align: center;
    justify-content: center;
    .hero-heading {
      // text-align: center;
      h1 {
        font-size: 64px;
        line-height: 56px;
      }
    }

    .hero-txt {
      margin-top: 1rem;
      p {
        font-size: 15px;
        line-height: 28px;
      }
    }
  }
}

@media (max-width: 375px) {

  .hero-container {
    padding: 11rem 0;
    .hero-heading {
      h1 {
        font-size: 40px;
        line-height: 56px;
      }
    }

    .hero-txt {
      margin-top: 1rem;
      p {
        font-size: 15px;
        line-height: 28px;
      }
    }
  }
}
