footer {
  background-color: #002529;

  .footer-container {
    flex-wrap: wrap;
    padding-bottom: 45px;
    > * {
      margin-top: 45px;
    }
    nav {
      margin-right: 6rem;
      .nav-btns {
        gap: 40px;
        > * {
          &:hover {
            color: $clr3;
          }
        }
      }
    }
    .footer-list {
      // margin-right: 6rem;
      font-family: Livvic;
      font-size: 15px;
      font-style: normal;
      font-weight: 600;
      line-height: 25px;
      letter-spacing: 0px;
      text-align: left;
      opacity: 0.6;
    }
  }

  .logo {
    font-size: 40px;
    margin-bottom: 25px;
  }
  .social-icons-wrapper {
    flex: 1;
    text-align: right;
    position: relative;
    min-width: 300px;
    .social-icons {
      // justify-content: flex-end;
      // align-items: ;
      position: absolute;
      top: 0;
      right: 0;

      > * {
        &:not(:first-child){

          margin-left: 15px;
        }
      }
    }

    p {
      position: absolute;
      right: 0;
      bottom: 0;
    }
  }
}

.orangeonhover {
  &:hover {
    stroke: $clr3;
    fill: $clr3;
  }
}

@media (max-width: 1020px) {
}

@media (max-width: 860px) {
  footer {
    
    .footer-container {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      nav {
        // margin-right: 2rem;
        margin: 0;
      }

    }

    .social-icons-wrapper {
      position: relative;
      text-align: center;
      .social-icons{
        justify-content: center;
       

        position: unset;
      }

      p{
        position: unset;
      }
    }
  }
}
