.stories-wrapper {
  background-color: $clr1;
  padding: 150px 0 0;

  background-image: url('../../assets/imgs/bg-pattern-home-4-about-3.svg');

  background-repeat: no-repeat;
  span {
    color: $clr4;
  }

  .stories-header {
    font-size: rem(48px);
    text-align: center;
  }

  .stories-container {
    text-align: center;
    gap: 45px;
    // flex: 1;
    .story {
      flex: 1;
      height: 100%;
      min-height: 300px;
      // min-width: 285px;
      padding: 3rem 0;
      position: relative;
      .quotes {
        position: absolute;
        z-index: 0;
        top: 1rem;
      }
      p {
        font-size: 15px;
        line-height: 25px;
        z-index: 1;
      }
      span {
        margin: 2rem 0;
      }
      .avatar {
        position: absolute;
        border-radius: 50%;
        width: 62px;
        height: 62px;
        border: 2px solid #c4fffe;
        bottom: 0;
      }
    }
  }
  .circle {
    background-image: url('../../assets/imgs/bg-pattern-about-2-1.svg');
    height: 150px;
    background-repeat: no-repeat;
    background-position-x: right;
    background-position-y: 55px;
    z-index: 0;
  }
}

@media (max-width: 860px) {
  .stories-wrapper {
    .stories-header {
      font-size: rem(32px);
    }

    .stories-container {
      flex-direction: column;

      .story {
        min-height: 250px;
        max-width: 575px;
        .quotes {
          top: 1.3rem;
        }

        span {
          margin: 1rem 0;
        }
      }
    }
  }
}

@media (max-width: 375px) {
  .stories-wrapper {
    .stories-container {
      .story {
        max-width: 325px;
        text-align: center;

        .quotes {
          top: 1rem;
        }
      }
    }
  }
}
